/* @jsxImportSource solid-js */
import { render } from "solid-js/web";
import { createSignal } from "solid-js";

console.log("üöÄ Script started loading");

function SimpleTest() {
  console.log("üì¶ SimpleTest component created");
  const [count, setCount] = createSignal(0);

  return (
    <div style={{ padding: "20px", border: "1px solid #ccc", margin: "20px" }}>
      <h2>Simple Solid.js Test</h2>
      <p>Count: {count()}</p>
      <button onClick={() => setCount(count() + 1)}>Increment</button>
    </div>
  );
}

// Create a simple custom element manually without solid-element
class SimpleTestElement extends HTMLElement {
  private dispose?: () => void;

  connectedCallback() {
    console.log("üîå SimpleTestElement connected");
    try {
      this.dispose = render(() => <SimpleTest />, this);
      console.log("‚úÖ Render successful");
    } catch (error) {
      console.error("‚ùå Render failed:", error);
    }
  }

  disconnectedCallback() {
    console.log("üîå SimpleTestElement disconnected");
    if (this.dispose) {
      this.dispose();
    }
  }
}

console.log("üìù About to register custom element");

try {
  // Register the custom element
  customElements.define("simple-test", SimpleTestElement);
  console.log("‚úÖ Custom element registered successfully");
} catch (error) {
  console.error("‚ùå Failed to register custom element:", error);
}

export { SimpleTest, SimpleTestElement };
